html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

nav,
ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

a,
span,
*:before,
*:after {
  display: inline-flex;
  text-decoration: none;
  color: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-if_lock {
  width: 1em;
  height: 1em;
  fill: initial;
}

.icon-logo {
  width: 5.55em;
  height: 1em;
  fill: initial;
}

.icon-menu-arrow {
  width: 1.33em;
  height: 1em;
  fill: #FFF;
}

@font-face {
  font-family: "sofiaproregular";
  src: url("../fonts/sofiaproregular-webfont.woff2") format("woff2"), url("../fonts/sofiaproregular-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "sofiaprosoftbold";
  src: url("../fonts/sofiaprosoftbold-webfont.woff2") format("woff2"), url("../fonts/sofiaprosoftbold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

body {
  background: #FFF;
  color: #fff;
  font-family: 'sofiaproregular',sans-serif;
}

.container {
  max-width: 750px;
  width: 100%;
  margin: 0 auto;
}

@media only screen and (max-width: 550px) {
  .container {
    display: block;
    text-align: center;
  }
}

.container_flex {
  display: flex;
}

.contact-heading {
  font-family: 'sofiaprosoftbold',sans-serif;
}

.contact-heading_footer {
  margin-bottom: 12px;
}

.contact-mail_text {
  font-family: 'sofiaprosoftbold',sans-serif;
}

.contact-mail_footer {
  margin-bottom: 7px;
}

.contact-tel_text {
  font-family: 'sofiaprosoftbold',sans-serif;
}

.contact-tel_footer {
  margin-bottom: 14px;
  font-size: 14px;
}

.communications_footer {
  line-height: 1;
}

.address_footer {
  line-height: 1.3;
  margin-bottom: 11px;
}

.info-corporate_footer {
  position: relative;
}

.info-corporate_footer:before {
  position: absolute;
  left: calc(100% + 34px);
  content: '';
  height: 100%;
  width: 1px;
  background: #929499;
}

.icon-menu-arrow {
  margin-left: 5px;
}

.submenu {
  display: none;
  position: absolute;
  left: auto;
  margin-top: 20px;
  padding-top: 5px;
  z-index: 100;
  background: #929499;
  width: 100%;
  text-align: center;
}

.submenu li {
  margin-bottom: 5px;
}

.submenu li:hover {
  color: red;
}

.form-data {
  font-size: 10px;
}

.icon-if_lock {
  fill: #000;
  width: 12px;
  height: 16px;
  margin-right: 5px;
}

.icon-menu-arrow {
  width: 8px;
  height: 6px;
  align-self: center;
}

.icon-logo {
  font-size: 22px;
  margin-left: 31px;
}

.header-main {
  padding: 2px 60px;
  display: flex;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  flex-wrap: wrap;
  font-family: 'sofiaproregular',sans-serif;
}

.nav-wrapper {
  margin-left: auto;
  position: relative;
  bottom: 15px;
}

@media only screen and (max-width: 800px) {
  .nav-wrapper {
    position: static;
  }
}

.nav {
  display: flex;
  flex-wrap: wrap;
}

@media only screen and (max-width: 550px) {
  .nav {
    justify-content: center;
  }
}

.nav__item {
  text-transform: uppercase;
  margin-right: 22px;
  font-size: 12px;
  position: relative;
  display: flex;
  justify-content: center;
  padding: 8px 0px;
}

.nav__item:hover:before {
  width: 100%;
}

@media only screen and (max-width: 800px) {
  .nav__item:hover:before {
    width: 50%;
  }
}

.nav__item:before {
  content: '';
  position: absolute;
  bottom: 0;
  width: 0;
  height: 3px;
  background: #2effc0;
  transition: all .3s ease;
}

.nav__list {
  display: flex;
  white-space: nowrap;
}

@media only screen and (max-width: 800px) {
  .nav__list {
    display: none;
  }
}

@media only screen and (max-width: 800px) {
  .nav__list_is-open {
    display: block;
  }
}

.baner {
  display: flex;
  align-items: center;
  min-height: 227px;
  padding-top: 204px;
  padding-bottom: 154px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 0 0;
  box-shadow: 1px 10px 8px rgba(0, 0, 0, 0.07);
}

@media only screen and (max-width: 800px) {
  .baner {
    padding-top: 50px;
  }
}

.baner__img {
  max-width: 100%;
}

@media only screen and (max-width: 800px) {
  .baner__img {
    padding-top: 325px;
  }
}

@media only screen and (max-width: 320px) {
  .baner__img {
    max-width: 20%;
  }
}

.baner__heading {
  color: #2effc0;
  font-size: 41px;
}

@media only screen and (max-width: 800px) {
  .baner__heading {
    text-align: center;
  }
}

@media only screen and (max-width: 320px) {
  .baner__heading {
    font-size: 30px;
  }
}

.lang-switcher {
  font-size: 12px;
  color: #fff;
  display: flex;
  justify-content: flex-end;
  margin-bottom: 12px;
}

.lang-switcher__item {
  margin-left: 12px;
  position: relative;
}

.lang-switcher__item:not(:last-child):before {
  content: "/";
  position: absolute;
  left: calc(100% + 6.5px);
}

.nav__btn {
  text-align: right;
  display: none;
}

@media only screen and (max-width: 800px) {
  .nav__btn {
    display: block;
  }
}

.btn-nav {
  border: 0;
  padding: 6px;
  cursor: pointer;
  border-radius: 4px;
  background: none;
  transition: all 1s ease;
}

.btn-nav:hover {
  background: #2effc0;
}

.btn-nav__line {
  display: block;
  width: 20px;
  height: 2px;
  border-radius: 1px;
  background: #000;
}

.btn-nav__line:not(:last-child) {
  margin-bottom: 2px;
}

.btn {
  border: none;
  border-radius: 50px;
  text-transform: uppercase;
  font-family: 'sofiaprosoftbold',sans-serif;
  transition: all 1s ease;
}

.btn_primary {
  background: #2effc0;
  font-size: 12px;
  padding: 7px 18px;
  color: #001029;
}

.btn_primary:hover {
  background: #001029;
  color: #2effc0;
}

@media only screen and (max-width: 800px) {
  .btn_primary {
    margin-top: 10px;
  }
}

.btn_submit {
  font-size: 15.5px;
  color: #d2d5d9;
  background: #001029;
  padding: 4px 45px 3px 45px;
  line-height: 2;
}

.btn_submit:hover {
  background: #2effc0;
}

.user-form {
  position: relative;
  bottom: 70px;
  background: #fff;
  border: 1px solid #fbfbfb;
  max-width: 260px;
  width: 100%;
  padding: 19px 34px 14px 25px;
  border-radius: 16px;
  box-shadow: 1px 10px 33px rgba(0, 0, 0, 0.07);
  margin-right: 67px;
  margin-left: 80px;
}

@media only screen and (max-width: 550px) {
  .user-form {
    margin: 0 auto;
  }
}

.user-form__area {
  color: #001029;
}

.user-form__area label {
  font-size: 12px;
  line-height: 2.1;
  font-family: 'sofiaprosoftbold',sans-serif;
  padding-left: 8px;
}

.user-form__area input {
  border: 1px solid #dedede;
  border-radius: 7px;
  padding: 10px;
  border-radius: 4px;
  box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.07);
  line-height: 1.57;
  outline: none;
  transition: .18s ease-out;
  width: 106%;
  margin-bottom: 10px;
}

.user-form__area input:hover {
  box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.02);
}

.user-form__area input:focus {
  color: #4b515d;
  border: 1px solid #B8B6B6;
  box-shadow: inset 1px 2px 4px rgba(0, 0, 0, 0.01), 0px 0px 8px rgba(0, 0, 0, 0.2);
}

.user-form__description {
  font-size: 15.5px;
  color: #00112a;
  line-height: 1.26;
}

.user-form p {
  margin-bottom: 21px;
}

select {
  width: 100%;
  border: 1px solid #dedede;
  border-radius: 4px;
  box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.07);
  margin-bottom: 46px;
}

select:focus, select:hover {
  background: #eeeeee;
}

select option {
  font-size: 16px;
  padding: 7px;
  text-align: center;
}

.select-wrapper {
  margin-top: 8px;
}

.user-contact-us {
  margin-bottom: 83px;
}

.user-contact-us__more {
  font-size: 15.5px;
  color: #001029;
  padding-top: 50px;
}

.contact-info {
  width: 200px;
}

@media only screen and (max-width: 550px) {
  .contact-info {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.contact-info > * {
  margin-bottom: 36px;
  line-height: 1.2;
}

.contact-info > * a:hover {
  color: #2effc0;
}

@media only screen and (max-width: 550px) {
  .contact-info > * {
    flex: 1 1 33%;
  }
}

@media only screen and (max-width: 550px) {
  .contact-info > :nth-child(odd) {
    margin-right: 5%;
  }
}

.contact-info__sales {
  margin-bottom: 38px;
}

.footer {
  font-size: 16.4px;
  background: #001029;
}

.footer a:hover {
  color: #2effc0;
}

.footer__wrap {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 46px;
  padding: 67px 0px;
}

@media only screen and (max-width: 820px) {
  .footer__wrap {
    grid-template-columns: repeat(2, 1fr);
    margin: 0 auto;
    padding: 70px 15px;
  }
}

.logo_footer {
  padding-top: 3px;
}

.copyright {
  background: #001029;
  display: flex;
  justify-content: center;
  padding-bottom: 22px;
  font-size: 11px;
  line-height: 1.1;
  color: #fff;
}

.button-data-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button-data-wrapper__description {
  margin-top: 30px;
  font-size: 10px;
  display: flex;
  align-items: center;
}
