.header-main {
	padding: 2px 60px;
	display: flex;
	// max-width: 2024px
	width: 100%;
	position: absolute;
	top: 0;
	left: 0;
	flex-wrap: wrap;
	@include sofproreg; }
.nav-wrapper {
	margin-left: auto;
	position: relative;
	bottom: 15px;
	@include r(800) {
		position: static; } }
.nav {
	display: flex;
	flex-wrap: wrap;
	@include r(550) {
		justify-content: center; }
	&__link {
 }		// white-space: nowrap
	&__item {
		text-transform: uppercase;
		margin-right: 22px;
		font-size: 12px;
		position: relative;
		display: flex;
		justify-content: center;
		padding: 8px 0px;
		&:hover:before {
			width: 100%;
			@include r(800) {
				width: 50%; } }
		&:before {
			content: '';
			position: absolute;
			bottom: 0;
			width: 0;
			height: 3px;
			background: $color-main;
			transition: all .3s ease; } }
	&__list {
		display: flex;
		// flex-wrap: wrap
		white-space: nowrap;
		@include r(800) {
			display: none; } } }
.nav__list_is-open {
	@include r(800) {
		display: block; } }

.baner {
	display: flex;
	align-items: center;
	min-height: 227px;
	padding-top: 204px;
	padding-bottom: 154px;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: 0 0;
	box-shadow: 1px 10px 8px rgba(0, 0, 0, 0.07);
	@include r(800) {
		padding-top: 50px; }
	&__img {
		max-width: 100%;

		@include r(800) {
			padding-top: 325px; }
		@include r(320) {
			max-width: 20%; } }
	&__heading {
		color: $color-main;
		font-size: 41px;
		@include r(800) {
			text-align: center; }
		@include r(320) {
			font-size: 30px; } } }
.lang-switcher {
	font-size: 12px;
	color: #fff;
	display: flex;
	justify-content: flex-end;
	margin-bottom: 12px;
	&__item {
		margin-left: 12px;
		position: relative;
		&:not(:last-child):before {
			content: "/";
			position: absolute;
			left: calc(100% + 6.5px); } } }
.nav__btn {
	text-align: right;
	display: none;
	@include r(800) {
		display: block; } }
.btn-nav {
	border: 0;
	padding: 6px;
	cursor: pointer;
	border-radius: 4px;
	background: none;
	transition: all 1s ease;
	&:hover {
		background: $color-main; }
	&__line {
		display: block;
		width: 20px;
		height: 2px;
		border-radius: 1px;
		background: #000;
		&:not(:last-child) {
			margin-bottom: 2px; } } }
.btn {
	border: none;
	border-radius: 50px;
	text-transform: uppercase;
	@include sofprosoftbold;
	transition: all 1s ease;
	&_primary {
		background: $color-main;
		font-size: 12px;
		padding: 7px 18px;
		color: #001029;
		&:hover {
			background: #001029;
			color: $color-main; }
		@include r(800) {
			margin-top: 10px; } }
	&_submit {
		font-size: 15.5px;
		color: #d2d5d9;
		background: #001029;
		padding: 4px 45px 3px 45px;
		line-height: 2;
		&:hover {
			background: $color-main; } } }

.user-form {
	position: relative;
	bottom: 70px;
	background: #fff;
	border: 1px solid #fbfbfb;
	max-width: 260px;
	width: 100%;
	padding: 19px 34px 14px 25px;
	border-radius: 16px;
	box-shadow: 1px 10px 33px rgba(0, 0, 0, 0.07);
	margin-right: 67px;
	margin-left: 80px;
	@include r(550) {
		margin: 0 auto; }

	&__area {
		color: #001029;
		label {
			font-size: 12px;
			line-height: 2.1;
			@include sofprosoftbold;
			padding-left: 8px; }
		input {
			border: 1px solid #dedede;
			border-radius: 7px;
			padding: 10px;
			border-radius: 4px;
			box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.07);
			line-height: 1.57;
			outline: none;
			transition: .18s ease-out;
			width: 106%;
			margin-bottom: 10px;
			&:hover {
				box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.02); }
			&:focus {
				color: #4b515d;
				border: 1px solid #B8B6B6;
				box-shadow: inset 1px 2px 4px rgba(0, 0, 0, 0.01), 0px 0px 8px rgba(0, 0, 0, 0.2); } } }
	&__description {
		font-size: 15.5px;
		color: #00112a;
		line-height: 1.26; }
	& p {
		margin-bottom: 21px; } }
select {
	width: 100%;
	border: 1px solid #dedede;
	border-radius: 4px;
	box-shadow: inset 1px 2px 8px rgba(0, 0, 0, 0.07);
	margin-bottom: 46px;
	&:focus,&:hover {
		background: #eeeeee; }
	option {
		font-size: 16px;
		padding: 7px;
		text-align: center; } }

.select-wrapper {
	margin-top: 8px; }
.user-contact-us {
	margin-bottom: 83px;
	&__more {
		font-size: 15.5px;
		color: #001029;
		padding-top: 50px; } }
.contact-info {
	width: 200px;
	@include r(550) {
		display: flex;
		width: 100%;
		flex-wrap: wrap;
		justify-content: center; }
	&>* {
		margin-bottom: 36px;
		line-height: 1.2;
		& a:hover {
			color: $color-main; }
		@include r(550) {
			flex: 1 1 33%; } }
	&>:nth-child(odd) {
		@include r(550) {
			margin-right: 5%; } }
	&__sales {
		margin-bottom: 38px; } }
.footer {
	font-size: 16.4px;
	background: #001029;
	& a {
		&:hover {
			color: $color-main; } }
	&__wrap {
		display: grid;
		grid-template-columns: repeat(4,1fr);
		grid-gap: 46px;
		padding: 67px 0px;
		@include r(820) {
			grid-template-columns: repeat(2,1fr);
			margin: 0 auto;
			padding: 70px 15px; } } }
.logo_footer {
	padding-top: 3px; }
.copyright {
	background: #001029;
	display: flex;
	justify-content: center;
	padding-bottom: 22px;
	font-size: 11px;
	line-height: 1.1;
	color: #fff; }
.button-data-wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	&__description {
		margin-top: 30px;
		font-size: 10px;
		display: flex;
		align-items: center; } }
