@include font('sofiaproregular', 'sofiaproregular-webfont');
@mixin sofproreg {
	font-family: 'sofiaproregular',sans-serif; }
@include font('sofiaprosoftbold', 'sofiaprosoftbold-webfont');
@mixin sofprosoftbold {
	font-family: 'sofiaprosoftbold',sans-serif; }
body {
	background: #FFF;
	color: #fff;
	@include sofproreg; }
.container {
	max-width: 750px;
	width: 100%;
	margin: 0 auto;
	@include r(550) {
		display: block;
		text-align: center; }
	&_flex {
		display: flex; } }

.contact-heading {
	@include sofprosoftbold;
	&_footer {
		margin-bottom: 12px; } }

.contact-mail {
	&_text {
		@include sofprosoftbold; }
	&_footer {
		margin-bottom: 7px; } }
.contact-tel {
	&_text {
		@include sofprosoftbold; } }




.contact-tel_footer {
	margin-bottom: 14px;
	@include r(320);
	font-size: 14px; }
.communications_footer {
	line-height: 1; }
.address_footer {
	line-height: 1.3;
	margin-bottom: 11px; }
.info-corporate_footer {
	position: relative;
	&:before {
		position: absolute;
		left: calc(100% + 34px);
		content: '';
		height: 100%;
		width: 1px;
		background: #929499; } }

.icon-menu-arrow {
	margin-left: 5px; }

.submenu {
	display: none;
	position: absolute;
	left: auto;
	margin-top: 20px;
	padding-top: 5px;
	z-index: 100;
	background: #929499;
	width: 100%;
	text-align: center;
	li {
		margin-bottom: 5px;
		&:hover {
			color: red; } } }



