.form-data {
	font-size: 10px; }
.icon-if_lock {
	fill: #000;
	width: 12px;
	height: 16px;
	margin-right: 5px; }
.icon-menu-arrow {
	width: 8px;
	height: 6px;
	align-self: center; }
.icon-logo {
	font-size: 22px;
	margin-left: 31px; }
